.App-header{
  height: 100px;
  display:flex;
  background:#f60;
  color:#fff;
  font-size:0.6rem;
  padding-left:20px; 
}
h1{
  color: #fff;
  margin: 0 auto;
  line-height: 100px;
  font-size: 30px;
}
div{
  box-sizing: border-box;
}
.list{
  padding:16px 20px;
  border-top:1px dashed #ccc;
  overflow:hidden;
  color:#5d5d5d;
}
.list:last-child{
  border-bottom:1px dashed #ccc;
}
.list button{
  width: 30px;
  height: 30px;
  float:right;
  border: 0;
  background: #f00;
  border-radius: 2px;
  color: #fff;
  font-size: 20px;
  font-weight: bold;
}
.todo{
  width: 400px;
  height: 48px;
  margin: 50px auto;
  display: flex;
  justify-content: space-between;
}
.todo>input{
  width: 300px;
  height: 40px;
  border: 1px #ccc solid;
  text-indent: 20px;
}
.todo>button{
  width: 80px;
  height: 44px;
  border: 0;
  color: #fff;
  font-weight: bold;
  border-radius: 3px;
  background: #f60;
  cursor: pointer;
}
h3{
  color:#333;
  cursor:pointer;
}
h3 span{
  padding-left: 20px;
  font-weight: normal;
  color:#f60; 
}
h3 span:first-child{
  cursor:pointer;
  color:#06f; 
}
.write-box{
  display:flex;
  justify-content:center;
  margin:16px 0;
}
.write-box input{
  padding:4px 8px;
  border:1px solid #ccc;
  height:20px;
}
.write-box button{
  height:30px;
  border:0px;
  background:#090909;
  color:#fff;
  border-radius:4px;
  margin-left:8px;
}
.detail{
  width: 600px;
  height: 600px;
  position: absolute;
  transform: translate(-50%,-50%);
  top: 50%;
  left: 50%;
  background:#f4f4f4;
  box-shadow:0px 3px 5px 0px rgba(0,0,0,0.3);
  margin:30px;
  padding:20px;
  border-radius:10px;
}
.detail h4{
  border-bottom:2px solid #5d5d5d;
  padding-bottom:5px;
}